  background-color: #81B3D2;
  transition: all 0.3s ease;
}

.main-nav__toggle-icon::after,
.main-nav__toggle-icon::before {
  position: absolute;
  content: "";
  width: 100%;
  height: 100%;
  background-color: #81B3D2;
  top: 8px;
  left: 0;
  transition: all 0.3s ease;
}

.main-nav__toggle-icon::before {
  top: -8px;
}

.main-nav--opened .main-nav__toggle-icon {
  background-color: transparent;
}

.main-nav--opened .main-nav__toggle-icon::after,
.main-nav--opened .main-nav__toggle-icon::before {
  background-color: #ffffff;
